import React from "react"
import { Box, TextField } from "@rp/react/web/mui/core"
import { Search } from "@rp/react/web/mui/icons"

type SearchBarProps = {
  searchQuery: string
  onSearchChange: (query: string) => void
}

function SearchBar({ searchQuery, onSearchChange }: SearchBarProps) {
  return (
    <Box sx={{ pb: 2, pr: 2 }}>
      <TextField
        fullWidth
        placeholder="Type a column name"
        value={searchQuery}
        onChange={e => onSearchChange(e.target.value)}
        slotProps={{
          input: {
            startAdornment: <Search sx={{ mr: 1, color: "text.secondary" }} />,
          },
        }}
        size="small"
        variant="outlined"
        sx={{
          "& .MuiOutlinedInput-root": {
            backgroundColor: "elevation.dp08",
            color: "text.primary",
            borderColor: "elevation.dp08",
            "&:hover": {
              borderColor: "primary.main",
            },
            "&.Mui-focused": {
              borderColor: "primary.main",
            },
          },
          "& .MuiOutlinedInput-input": {
            color: "text.primary",
            "&::placeholder": {
              color: "text.secondary",
              opacity: 1,
            },
          },
        }}
      />
    </Box>
  )
}

export { SearchBar }
